import Product from "./Product";
import {useEffect, useState} from "react"
import axios from "axios"

function Products(){

    const [SingleProduct,setProduct] = useState([]);

    useEffect(()=>{
        axios.get("https://fakestoreapi.com/products")
        .then((res)=>{setProduct(res.data)})
    },[])

    return (
        <>
            <div id="products-section" className="container" style={{marginTop:"10rem"}}>
                <div className="row" style={{dispaly:"flex",justifyContent:"center"}}> 
                        <form className="w-75">   
                        <label htmlFor="default-search" className="mb-2 text-sm font-medium text-gray-900 sr-only dark:text-white">Search</label>
                            <div className="relative">
                            <div className="absolute inset-y-0 left-0 flex items-center pl-3 pointer-events-none">
                                <svg className="w-4 h-4 text-gray-500 dark:text-gray-400 ms-2" aria-hidden="true" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 20 20">
                                <path stroke="currentColor" strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="m19 19-4-4m0-7A7 7 0 1 1 1 8a7 7 0 0 1 14 0Z" />
                                </svg>
                            </div>
                            <input type="search" id="default-search" className="rounded-0 ps-5 w-full p-4 text-sm text-gray-900 border border-gray-300 rounded-lg bg-gray-50 focus:ring-blue-500 focus:border-blue-500 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" placeholder="Search Products..." required />
                            <button type="submit" className="rounded-0 me-2 mb-2 text-white  absolute right-2.5 bottom-2.5 bg-blue-700 hover:bg-blue-800 focus:ring-4 focus:outline-none focus:ring-blue-300 font-medium rounded-lg text-sm px-4 py-2 dark:bg-blue-600 dark:hover:bg-blue-700 dark:focus:ring-blue-800 bg-primary">Search</button>
                            </div>
        
                        </form>

                </div>
                <div className="row" style={{dispaly:"flex",justifyContent:"center"}}>
                    
                </div>
                <div className="row" style={{dispaly:"flex",justifyContent:"center"}}>
                    
                    {
                        SingleProduct.map((product)=>{
                            return(
                                <>
                                <Product product={product}/>
                                </>
                            )
                        })
                    }
                    
                    
                </div>
            </div>
        </>
    )
}

export default Products;